import { FC, Fragment } from "react";
import { useNavigate } from "react-router-dom";
import { RoutePath } from "@Client/routers";
import SearchBar from "@Client/components/search-bar";


interface BookStorePageProps {

}

// 书城
const BookStorePage: FC<BookStorePageProps> = () => {
  const nav = useNavigate();
  return (
    <Fragment>
      <div className='flex items-center justify-center h-full bg-leaf-secondary dark:bg-leaf-secondary-dark'>
        {/* search bar */}
        <SearchBar placeholder='请输入书名或作者名' onFocus={() => nav(RoutePath.BookSearch)}>
          <div className='btn rounded-none py-0 pr-0 font-bold border-l'>书城</div>
        </SearchBar>
      </div>
    </Fragment>
  );
}

export default BookStorePage;
